        <div class="forget_password">
            <el-dialog :visible.sync="centerDialogVisible" width="500px" center :close-on-click-modal="false">
                <el-form ref="forget_password" class="marginTop" :rules="rules" :model="form" action="">
                    <el-form-item prop="username">
                        <el-input class="el-inputs" v-model="form.username" placeholder=""></el-input>
                        <img src="../../assets/images/login/username.png" />
                    </el-form-item>
                    <el-form-item prop="usercode">
                        <el-input type="usercode" v-model="form.usercode" placeholder=""></el-input>
                        <span class="getCode" @click="forgetCode"></span>
                    </el-form-item>
                    <el-form-item prop="userpassword">
                        <el-input type="password" v-model="form.userpassword" placeholder=""></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="form.password" placeholder=""></el-input>
                        <img src="../../assets/images/login/password.png" />
                    </el-form-item>
                    <el-form-item>
                        <span class="register_footer">
                            <el-button type="primary" @click="editPassword('from')"></el-button>
                        </span>
                    </el-form-item>
                </el-form>

            </el-dialog>
        </div>
    </div>
</template>
<script>
    export default {
         data() {
            // 
            var checkMoblie = (rule, value, callback) => {
                if (!value.trim()) {
                    return callback(new Error(''))
                }
                if (!/^1[34578]\d{9}$/.test(value)) {
                    return callback(new Error(''))
                } else {
                    callback()
                }
            }
            // 
            var validatePass1 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.form.password !== '') {
                        this.$refs.register.validateField('password1');
                    }
                    callback();
                }
              };
                var validatePass = (rule, value, callback) => {
                    if (value === '') {
                        callback(new Error(''));
                    } else {
                        if (this.form.password !== '') {
                            this.$refs.forget_password.validateField('password');
                        }
                        callback();
                    }
                  };
                var validatePass2 = (rule, value, callback) => {
                    if (value === '') {
                        callback(new Error(''));
                    } else if (value !== this.form.userpassword) {
                        callback(new Error('!'));
                    } else {
                        callback();
                    }
                };
            return {
                dialogVisible: false,
                form: {
                    username: '',
                    userpassword: '',
                    usercard: '',
                    usercode: '',
                    password: '',
                    password1: ''

                },
                activeName: 'second',
                loading: false,
                centerDialogVisible: false,
                centerDialog: false,
                centerVisible: false,
                rules: {
                    username: [{
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        {
                            validator: checkMoblie,
                            trigger: 'blur'
                        }
                    ],
                    usercode: [{
                            required: true,
                            message: '',
                            trigger: 'blur'
                        },
                        {
                            min: 6,
                            max: 6,
                            message: '6',
                            trigger: 'blur'
                        }
                    ],
                    userpassword: [{
                            required: true,
                            validator: validatePass,
                            trigger: 'blur'
                        },
                        {
                            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                            message: '816'
                        }
                    ],
                    password: [{
                        required: true,
                        validator: validatePass2,
                        trigger: 'blur'

                    },
                     {
                         pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                         message: '816'
                     }
                    ],
                    password1: [{
                        required: true,
                        validator: validatePass2,
                        trigger: 'blur'

                    },
                     {
                         pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/,
                         message: '816'
                     }
                    ]
                    // usercard: [{
                    //         required: true,
                    //         message: '',
                    //         trigger: 'blur'
                    //     }

                    // ]
                }
            }
        },
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
            },
            // 
            getCode() {
                this.axios.post(process.env.API_HOST + '/api/sms/send', {
                    mobile: this.form.username,
                    event: 1,
                }).then((data) => {
                        let e = JSON.parse(data.data)
                        if (e.status == 'ok') {
                            this.$message({
                                message: '',
                                center: true,
                                type: 'success',
                                duration:1000,
                            });
                        } else {
                            this.$alert(e.msg, '', {
                                confirmButtonText: ''
                            })
                        }
                    }).catch((error) => {
                        console.log(error)
                    })
            },
            // 
            registerform() {
                this.$refs.register.validate((status) => {
                    if (status) {
                        this.axios.post(process.env.API_HOST + '/api/user/register', {
                            mobile: this.form.username,
                            password: this.form.userpassword,
                            varcode: this.form.usercode,
                            //card_number: this.form.usercard,
                        })
                            .then((data) => {
                                console.log(data)
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    customClass: 'Boxmessage'
                                    const weiXinUrl = require("../../assets/images/login/sussecd.png");
                                    this.$message({
                                        dangerouslyUseHTMLString: true,
                                        message: "<img id='success' src=" + weiXinUrl + ">",
                                        duration: 2000
                                    });
                                    setTimeout(function(){
                                        location.reload()
                                    },3000)
                                } else {
                                    this.$alert(e.msg, '', {
                                        confirmButtonText: ''
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })
                        // this.loading = true
                    } else {
                        console.log(status)
                    }
                })
            },
            // 
            loginform() {
                this.$refs.login.validate((status) => {
                    if (status) {
                        this.axios.post(process.env.API_HOST + '/api/user/login',
                            {
                                mobile: this.form.username,
                                password: this.form.userpassword,
                                card_number:this.form.usercard
                            }
                        )
                            .then((data) => {
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    sessionStorage.setItem("login_token", e.data.token);
                                    sessionStorage.setItem("userInfo", e.data.username);
                                    sessionStorage.setItem("usercard", e.data.card_number);
                                    this.$message({
                                        message: '',
                                        center: true,
                                        type: 'success',
                                        duration:2000
                                    });
                                    this.$router.push('/')
                                } else {
                                    this.$alert(e.msg, '', {
                                        confirmButtonText: ''
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })

                    } else {
                        console.log(status)
                    }
                })
            },
            // 
            forgetCode() {
                this.axios.post(process.env.API_HOST + '/api/sms/send', {
                    mobile: this.form.username,
                    event: 2
                })
                    .then((data) => {
                        let e = JSON.parse(data.data)
                        if (e.status == 'ok') {
                            this.$message({
                                message: '',
                                center: true,
                                type: 'success',
                                duration:1000
                            });
                        } else {
                            this.$alert(e.msg, '', {
                                confirmButtonText: ''
                            })
                        }
                    }).catch((error) => {
                        console.log(error)
                    })
            },
            // 
            editPassword(forget_password) {
                this.$refs.forget_password.validate((valid) => {
                    if (valid) {
                        this.axios.post(process.env.API_HOST + '/api/user/forget_pass', {
                            mobile: this.form.username,
                            password: this.form.userpassword,
                            varcode: this.form.usercode
                        })
                            .then((data) => {
                                let e = JSON.parse(data.data)
                                if (e.status == 'ok') {
                                    const weiXinUrl = require("../../assets/images/login/pass.png");
                                    this.$message({
                                        dangerouslyUseHTMLString: true,
                                        message: "<img id='success' src=" + weiXinUrl + ">",
                                        duration: 2000
                                    });
                                    setTimeout(function(){
                                        location.reload()
                                    },3000)
                                } else {
                                    this.$alert(e.msg, '', {
                                        confirmButtonText: ''
                                    })
                                }
                            }).catch((error) => {
                                console.log(error)
                            })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });

            }
        }
    }
</script>
<style src="../../assets/css/login.css" scoped>
</style>
<style>
