<template>
    <div class="">
        <div class="top">
            <el-checkbox class="left">
                <el-input class="left_input" v-model="input" placeholder="1000"></el-input>
                <span> - </span>
                <el-input class="left_input" v-model="input2" placeholder="1000"></el-input>
            </el-checkbox>
            <el-checkbox class="left">
                <el-input class="left_input" v-model="input3" placeholder=""></el-input>
            </el-checkbox>
            <el-checkbox class="left"><a class="fontSize">()</a>
                <el-input class="left_input" v-model="input4" placeholder=""></el-input>
            </el-checkbox>
            <el-checkbox class="left">
                <el-input class="left_input" v-model="input5" placeholder=""></el-input>
            </el-checkbox>
        </div>
        <div class="middle">
            <el-checkbox class="left">
                <el-button type="info"></el-button>
            </el-checkbox>
            <el-checkbox class="left">
                <el-button type="info"></el-button>
            </el-checkbox>
            <el-checkbox class="left">
                <el-button type="info"></el-button>
            </el-checkbox>
            <el-checkbox class="left">
                <el-button type="info"></el-button>
            </el-checkbox>
        </div>
        <div class="bottom">
            <el-checkbox class="left">3c</el-checkbox>
            <el-checkbox class="left">++</el-checkbox>
            <el-checkbox class="left"></el-checkbox>
            <el-checkbox class="left"></el-checkbox>
            <el-checkbox class="left"></el-checkbox>
        </div>
    </div>
</template>
<script>
    export default {
        name: "sjgl",
        data() {
            return {
                input: '',
                input2: '',
                input3: '',
                input4: '',
                input5: '',
            }
        }
    }
</script>

<style scoped>
    .top,.middle,.bottom {
        margin-bottom: 13px;
        font-size: 10px;
        margin-left: 5px;
    }
    .left {
        margin-right: 15px;
    }
    .left .fontSize {
        font-size: 12px;
    }
    .left /deep/ .el-checkbox__label {
        padding-left: 3px;
    }
    .el-checkbox /deep/ .left_input{
        width: 40px;
        padding: unset;
    }
    /deep/ .left_input .el-input__inner {
        width: 44px;
        height: 22px;
        padding: unset;
        text-align: center;
    }
    .middle button {
        width: 40px;
        height: 22px;
        padding: unset;
        background-color: #303030;
    }
    .middle .left{
        margin-right: 20px;
    }
</style>



