<template>
    <div class="">
        <div class="main_img">
            <a></a>
            <select value="" class="basics_option">
                <option></option>
                <option></option>
            </select>
            <el-checkbox class="left"></el-checkbox>
            <a class="basics_margin">,<input class="basics_input"/></a>
            <el-checkbox class="left"></el-checkbox>
            <el-checkbox>
                <input class="basics_input"/>
            </el-checkbox>
        </div>

        <div class="middle">
            <el-checkbox class="middle_detail"></el-checkbox>
            <el-checkbox class="middle_set">/</el-checkbox>
        </div>

        <div class="bottom">
            <el-checkbox>,<input class="input_width"/>
                <select value="" class="bottom_option">
                    <option></option>
                    <option></option>
                </select>
                <a>,<input class="input_width"/>
                    <select value="()" class="bottom_option2">
                        <option>()</option>
                    </select>
                </a>
            </el-checkbox>
        </div>
    </div>
</template>

<script>
    export default {
        name: "imgset",
        data() {
            return {

            }
        }
    }
</script>
<style scoped>
    .main_img, .middle, .bottom {
        margin-left: 5px;
    }
    .main_img .basics_option {
        width: 104px;
        height: 25px;
    }
    .main_img .left {
        margin-left: 20px;
    }
    .main_img .basics_input, .bottom .input_width {
        width: 40px;
        height: 24px;
        border: 1px solid #ccc;
    }
    .main_img .basics_input:nth-child(1), .bottom .input_width {
        margin: 0 5px;
    }
    .middle, .bottom {
        margin-top: 10px;
    }
    .middle .middle_set {
        color:rgba(254,81,2,1);
    }
    .bottom .bottom_option, .bottom .bottom_option2 {
        width: 170px;
        height: 25px;
    }
</style>



